<template lang="pug">
Layout.register-page(v-if='valid')
  Row.register-header
    h1 完善信息
  Row.register-body
    .register-form
      Form(:model='userInfo', :rules='validators')
        FormItem(label='邮箱地址', :label-width='80', prop='email')
          Row(type='flex', :gutter='18')
            Input(v-model='userInfo.email', placeholder='请输入邮箱地址', disabled='')
        FormItem(label='姓名', :label-width='80', prop='name')
          Row(type='flex', :gutter='18')
            Input(v-model='userInfo.name', placeholder='请输入姓名')
        FormItem(label='验证码', :label-width='80', prop='captcha')
          Row(type='flex', :gutter='18')
            Input(v-model='userInfo.captcha', placeholder='请输入验证码')
            img(:src='captchaUrl', alt='验证码', @click='refreshCaptcha')
        FormItem(label='手机号码', :label-width='80', prop='phone')
          Row(type='flex', :gutter='18')
            Input(v-model='userInfo.phone', placeholder='请输入手机号码')
            Button(type='primary', :loading='smsWait', @click='sendSms')
              span(v-if='!smsWait') 发送验证码
              span(v-else='') {{`${smsWaitTime}秒后重试`}}
        FormItem(label='验证码', :label-width='80', prop='smsVcode')
          Row(type='flex', :gutter='18')
            Input(v-model='userInfo.smsVcode', placeholder='请输入验证码')
    Button(type='success', @click.native='submit')
      | 确定
</template>

<script>
import { sendSmsCode, getCaptcha, validateCaptcha } from '@/api/sms'
import { activateUser, validateUrl } from '@/api/userRegister'

export default {
  name: 'register',
  data () {
    return {
      smsWait: false,
      smsWaitTime: 60,
      userInfo: {
        email: '',
        name: '',
        phone: '',
        smsVcode: '',
        id: null,
        captcha: '',
        code: '',
        activationCode: ''
      },
      validators: {
        name: [
          { required: true, message: '请输入姓名', trigger: 'change' }
        ],
        phone: [
          { required: true, message: '请输入手机号码', trigger: 'change' }
        ],
        smsVcode: [
          { required: true, message: '请输入短信验证码', trigger: 'blur' }
        ],
        captcha: [
          { required: true, message: '请输入验证码', trigger: 'blur' }
        ]
      },
      captchaUrl: '',
      captChaUid: '',
      valid: false
    }
  },
  async created () {
    this.userInfo.activationCode = this.$route.query.code
    if (!this.userInfo.activationCode) {
      this.$Modal.error({
        content: '非法访问！',
        onOk: () => {
          this.$router.replace({ name: 'Login' })
        }
      })
      return
    }
    const resp = await validateUrl({ activationCode: this.userInfo.activationCode })
    if (resp.response.success) {
      this.valid = true
      this.userInfo.email = resp.data
    } else {
      this.valid = false
      this.$Modal.error({
        content: '此链接已失效。',
        onOk: () => {
          this.$router.replace({ name: 'Login' })
        }
      })
    }
    this.refreshCaptcha()
  },
  computed: {
  },
  watch: {
  },
  methods: {
    async sendSms () {
      if (this.userInfo.captcha.replace(/\s/g, '') === '') {
        this.$Message.warning('请输入验证码！')
        return
      }
      if (('' + this.userInfo.phone).replace(/\s/g, '') === '') {
        this.$Message.warning('请输入手机号码！')
        return
      }
      const validateResp = await validateCaptcha({ captcha: this.userInfo.captcha, uuid: this.captchaUid })
      if (!validateResp.response.success) {
        this.$Message.error('验证码错误，请重新输入！')
        return
      }
      sendSmsCode({ phone: this.userInfo.phone, type: 1, captcha: this.userInfo.captcha, captchaUuid: this.captchaUid }).then(resp => {
        if (resp.response.success) {
          this.$Message.success('短信发送成功！')
          this.smsWait = true
          this.smsWaitTime = 60
          const timer = setInterval(() => {
            if (this.smsWaitTime <= 1) {
              clearInterval(timer)
              this.smsWait = false
            }
            this.smsWaitTime = this.smsWaitTime - 1
          }, 1000)
        } else {
          this.$Message.error(resp.response.msg)
        }
      })
    },
    refreshCaptcha () {
      getCaptcha().then(resp => {
        this.captchaUrl = `data:image;base64,${resp.data.image}`
        this.captchaUid = resp.data.uuid
      })
    },
    submit () {
      activateUser(this.userInfo).then(resp => {
        if (resp.response.success) {
          this.$Message.success('激活成功！')
          this.$store.commit('user/setToken', resp.data.token)
          this.$store.commit('user/setUserInfo', resp.data.user)
          this.$router.push({ name: 'Home' })
        } else {
          this.$Message.error(resp.response.msg)
          this.refreshCaptcha()
        }
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
