  min-width 50vw
  min-height 50vh
  max-height 70vh
  position absolute
  top 10%
  left 50%
  transform translateX(-50%)
  text-align center
  overflow auto
  min-height 27rem

  .register-body
    background-color #ffffff
    border-radius 0.2rem
    box-shadow 0 0 1px 0 rgba(0,0,0,0.1)
    padding 0.5rem
    min-width 400px
    min-height 23rem
    .register-form
      width 100%
      display flex
      justify-content center
      .qrcode-row
        flex-direction column
        align-items center
        .success-icon
          color rgb(41, 189, 110)
          font-size 1rem
</style>
